@import url("https://fonts.googleapis.com/css?family=Satisfy&display=swap");
@import url("https://fonts.googleapis.com/css?family=Overpass&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Overpass";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html {
  scroll-behavior: smooth;
  overflow-x: hidden; }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #2c5d66;
  border: 1px solid #f1f1f1;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@keyframes fadeout {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes fadein {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.contact {
  margin-top: 70px; }
  .contact .contact__head {
    align-items: center;
    color: white;
    width: 200px;
    margin: auto;
    background-color: #2c5d66;
    position: relative;
    text-align: center;
    padding: 10px 10px 7px 10px;
    z-index: 10;
    border-radius: 10px; }
  .contact .contact__form__div {
    margin-top: 40px; }
    .contact .contact__form__div .contact__form {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 40%;
      margin: auto;
      margin-bottom: 20px; }
      .contact .contact__form__div .contact__form .contact__form__input {
        width: 80%;
        color: black;
        font-size: 1rem;
        border-radius: 10px;
        padding: 5px 10px;
        margin: 10px 0 0px 0px;
        display: inline-block;
        background-color: rgba(182, 182, 182, 0.342);
        border: 0px;
        text-decoration: none;
        font-family: "K2D", sans-serif;
        -webkit-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
        -moz-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
        box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54); }
        .contact .contact__form__div .contact__form .contact__form__input:focus {
          outline: none; }
      .contact .contact__form__div .contact__form .contact__form__button {
        border-radius: 30px;
        font-size: 20px;
        padding: 5px 10px;
        margin: 10px;
        background: #2c5d66;
        color: white;
        cursor: pointer; }
        .contact .contact__form__div .contact__form .contact__form__button:hover {
          background: #b7cff1; }
        .contact .contact__form__div .contact__form .contact__form__button:focus {
          -webkit-box-shadow: 0px 0px 5px 0px #5b50f2;
          -moz-box-shadow: 0px 0px 5px 0px #5b50f2;
          box-shadow: 0px 0px 5px 0px #5b50f2;
          outline: none; }
    .contact .contact__form__div textarea {
      width: 80%;
      background: rgba(182, 182, 182, 0.342);
      font-size: 1rem;
      margin: 23px;
      padding: 10px 0px 0px 10px;
      font-family: "K2D", sans-serif;
      border: none;
      -webkit-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
      -moz-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
      box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
      border-radius: 10px; }
      .contact .contact__form__div textarea:focus {
        outline: none; }

@media screen and (max-width: 960px) {
  .contact__form {
    width: 100% !important; } }
