.footer {
  position: relative;
  text-align: center;
  background: #e6e6e6;
  margin-top: 4.5rem;
  padding: 0.5rem 0;
  z-index: 10; }
  .footer:before {
    height: 72px;
    content: "";
    position: absolute;
    background-color: inherit;
    padding: 0;
    display: block;
    z-index: -666;
    width: 100%;
    height: 8.5rem;
    -webkit-transform: skew(0deg, -2deg);
    transform: skew(0deg, -2deg);
    top: -24px;
    top: -4.5rem;
    -webkit-box-shadow: 0px -6px 8px -10px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px -6px 8px -10px rgba(0, 0, 0, 0.75);
    box-shadow: 0px -6px 8px -10px rgba(0, 0, 0, 0.75); }
  .footer .credits {
    text-align: center;
    color: #585757; }
    .footer .credits .link {
      color: #2c5d66;
      text-decoration: none; }
      .footer .credits .link:hover {
        color: #878ee2;
        list-style: line; }
  .footer .footer__container .footer__container__icons {
    color: #2c5d66;
    text-decoration: none;
    font-size: 30px;
    margin: 30px 20px; }
    .footer .footer__container .footer__container__icons:hover {
      color: #878ee2;
      list-style: line; }
