@import url("https://fonts.googleapis.com/css?family=Satisfy&display=swap");
@import url("https://fonts.googleapis.com/css?family=Overpass&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Overpass";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html {
  scroll-behavior: smooth;
  overflow-x: hidden; }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #2c5d66;
  border: 1px solid #f1f1f1;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@keyframes fadeout {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes fadein {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.header {
  height: 80px;
  color: white;
  display: flex;
  width: 100%;
  position: absolute;
  background-color: #2c5d66; }
  .header .navigation__header {
    flex: 1;
    margin: auto;
    justify-content: center;
    flex-direction: column;
    text-align: center; }
    .header .navigation__header .navigation__header__logo {
      margin: 0;
      font-size: 40px;
      margin-bottom: -10px; }
    .header .navigation__header .navigation__header__logo2 {
      margin: -30px; }
  .header .navigation__center {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center; }
  .header .navigation__container {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center; }
    .header .navigation__container .link {
      margin: 0 30px;
      color: white;
      font-size: 30px; }
      .header .navigation__container .link:hover {
        color: #878ee2; }

.active {
  border-bottom: 2px solid #d83ddd; }

.nav {
  background-color: rgba(255, 255, 255, 0.774);
  z-index: 10;
  position: absolute;
  display: flex;
  width: 100%;
  margin-top: 80px;
  top: 0;
  -webkit-box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75); }
  .nav ul {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 100%; }
    .nav ul li {
      padding: 10px;
      list-style: none;
      margin: 0 30px; }
      .nav ul li a {
        cursor: pointer;
        flex: 1;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #585757; }
        .nav ul li a:hover {
          color: #d83ddd; }

.fixed-nav__logo {
  margin: 0;
  padding: 0;
  display: none;
  cursor: pointer; }

#fixed-nav {
  position: fixed;
  z-index: 11;
  margin-top: 0;
  width: 100%; }

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 15px;
  top: 15px;
  display: none; }
  .bm-burger-button .bm-burger-bars {
    background: #222222;
    border-radius: 10px; }
  .bm-burger-button .bm-burger-bars-hover {
    background: #2c5d66;
    opacity: 1 !important; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 40px;
  width: 40px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #d83ddd; }

.hamburger__header {
  font-family: "Overpass";
  width: 100%;
  text-align: center; }

/* General sidebar styles */
.bm-menu {
  background: #373a47;
  font-size: 1.5em;
  display: flex;
  justify-content: center;
  align-items: center; }
  .bm-menu .bm-item-list {
    color: white; }
  .bm-menu .bm-item {
    display: inline-block;
    display: flex; }
    .bm-menu .bm-item:focus {
      outline: none; }
    .bm-menu .bm-item .hamburger__ul {
      list-style: none;
      margin: 100px 0;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center; }
    .bm-menu .bm-item a {
      margin: 30px 0;
      width: auto;
      cursor: pointer; }
      .bm-menu .bm-item a:hover {
        color: #878ee2; }

.menu-wrap {
  display: none;
  position: fixed;
  width: 100%;
  background-color: white;
  height: 60px;
  z-index: 100;
  -webkit-box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75); }
  .menu-wrap .menu-wrap__header {
    font-family: "Overpass";
    margin: 5px 0 0 30px;
    display: flex;
    flex-direction: column;
    cursor: pointer; }
  .menu-wrap .menu-wrap__text {
    font-family: "Overpass";
    font-size: 11px;
    margin-left: 50px;
    position: absolute;
    margin-top: -10px;
    cursor: pointer; }

.hamburger__links {
  justify-content: flex-end;
  margin-top: 100px; }
  .hamburger__links .link {
    color: white;
    margin: 0 20px; }
    .hamburger__links .link:hover {
      color: #878ee2; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
  display: none;
  top: 0; }

@media screen and (max-width: 960px) {
  .header,
  .nav,
  .fixed-nav {
    display: none !important; }
  .bm-burger-button {
    display: block;
    display: flex; }
  .hamburger__ul {
    flex-direction: column; }
  .bm-menu-wrap {
    display: block; }
  .menu-wrap {
    display: block; } }
