  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.location {
  width: 100%;
  background: url(../../images/marbles.webp) center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  padding-top: 40px;
  padding-bottom: 60px;
  margin-bottom: -30px;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
  -moz-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
  box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54); }
  .location .location__div {
    width: 60%;
