    -webkit-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
    -moz-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
    box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #d83ddd; }
    .location .location__div .location__icon {
