@import url("https://fonts.googleapis.com/css?family=Satisfy&display=swap");
@import url("https://fonts.googleapis.com/css?family=Overpass&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Overpass";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html {
  scroll-behavior: smooth;
  overflow-x: hidden; }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #2c5d66;
  border: 1px solid #f1f1f1;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@keyframes fadeout {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes fadein {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.information {
  width: 80%;
  margin: auto;
  margin-top: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 70px; }
  .information .information__content .information__content__title {
    align-items: center;
    color: white;
    width: 200px;
    margin: auto;
    background-color: #2c5d66;
    position: relative;
    text-align: center;
    padding: 10px 10px 7px 10px;
    z-index: 10;
    border-radius: 10px; }
  .information .information__content .information__content__info {
    margin-top: 40px;
    width: 80%;
    margin: auto; }
    .information .information__content .information__content__info .information__content__info__text {
      margin: 50px 0; }
      .information .information__content .information__content__info .information__content__info__text .information__content__info__text__link {
        list-style: none;
        font-weight: bold;
        color: #2c5d66; }
        .information .information__content .information__content__info .information__content__info__text .information__content__info__text__link:hover {
          color: #878ee2; }
    .information .information__content .information__content__info .information__content__info__boxes {
      display: flex;
      flex-wrap: wrap;
      margin: auto; }
      .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__left {
        flex-wrap: wrap;
        flex: 1;
        justify-content: center;
        margin-bottom: 30px;
        min-width: 300px;
        max-width: 450px;
        width: 100%;
        margin-left: 40px; }
        .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__left .plupp {
          justify-content: row; }
        .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__left .information__content__button {
          border-radius: 30px;
          font-size: 20px;
          padding: 5px 10px;
          margin: 10px;
          background: #2c5d66;
          color: white;
          cursor: pointer; }
          .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__left .information__content__button:hover {
            background: #b7cff1; }
          .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__left .information__content__button:focus {
            -webkit-box-shadow: 0px 0px 5px 0px #5b50f2;
            -moz-box-shadow: 0px 0px 5px 0px #5b50f2;
            box-shadow: 0px 0px 5px 0px #5b50f2;
            outline: none; }
      .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__right {
        flex-wrap: wrap;
        flex: 1;
        justify-content: center;
        margin: auto; }
        .information .information__content .information__content__info .information__content__info__boxes .information__content__info__boxes__right .information__content__info__boxes__box__image {
          width: 100%;
          max-width: 500px;
          min-width: 300px;
          margin: auto;
          border-radius: 10px; }

.year {
  width: 80%; }
  .year .year__header {
    color: #d83ddd; }
  .year .year__content .year__content__ul {
    list-style: none;
    padding: 0; }

@media screen and (max-width: 960px) {
  .information__content {
    width: 80% !important; }
  .information__content__info__boxes__box__image {
    margin: auto !important; }
  .information__content__info {
    width: 100% !important; }
  .information {
    width: 100% !important; }
  .information__content__info__boxes__left {
    margin-left: 0 !important; } }

@media screen and (max-width: 500px) {
  .information__content__info__boxes__box__image {
    width: 100%;
    margin: 0px !important;
    min-width: 200px !important;
    border-radius: 10px; }
  .year__content {
    width: 100% !important; }
  .information__content__info__boxes__left {
    min-width: 200px !important; }
