.hero {
  background: url(../../images/marbles.webp) no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #e6e6e6;
  color: #2e2e2e;
  -webkit-box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 7px 8px -10px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column; }
  .hero .hero__content {
    margin-top: 60px;
    height: 220px;
    width: 200px;
    border-radius: 50%;
    border: 4px solid #2c5d66;
    border-radius: 50%;
    max-height: 100%;
    background-color: rgba(255, 255, 255, 0.801);
    text-align: center; }
    .hero .hero__content .hero__picture {
      max-width: 100%;
      max-height: 100%;
      object-fit: contain;
      border-radius: 50%; }
  .hero .hero__text {
    justify-content: center;
    text-align: center; }
    .hero .hero__text .hero__text__bold {
      color: #d83ddd; }
    .hero .hero__text .hero__text__header {
      margin-top: 40px; }
    .hero .hero__text .hero__text__button {
      margin-top: 20px;
      font-size: 20px;
      background-color: #2c5d66;
      color: white;
      cursor: pointer;
      border-radius: 30px;
      padding: 10px 15px; }
      .hero .hero__text .hero__text__button:hover {
        background: #b7cff1; }
      .hero .hero__text .hero__text__button:focus {
        outline: none; }

@media screen and (max-width: 960px) {
  .hero__text__button__icon {
    color: #d83ddd; } }
