@import url("https://fonts.googleapis.com/css?family=Satisfy&display=swap");
@import url("https://fonts.googleapis.com/css?family=Overpass&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Overpass";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html {
  scroll-behavior: smooth;
  overflow-x: hidden; }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #2c5d66;
  border: 1px solid #f1f1f1;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@keyframes fadeout {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes fadein {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

* {
  box-sizing: border-box; }

article,
aside,
footer,
header,
nav,
section {
  display: block; }

.main {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center; }
  .main .main__header {
    flex: 100%;
    height: 100vh;
    transition: height 999999s; }
  .main .main__top {
    flex: 100%; }
  .main .main__first {
    flex: 100%; }
  .main .main__two {
    flex: 100%; }
  .main .main__three {
    flex: 100%; }
  .main .main__fourth {
    flex: 100%; }
  .main .main__fifth {
    flex: 100%; }

@media screen and (max-width: 960px) {
  .app__main {
    margin: 0; }
  .main__three {
    margin-left: 0 !important; } }
