@import url("https://fonts.googleapis.com/css?family=Satisfy&display=swap");
@import url("https://fonts.googleapis.com/css?family=Overpass&display=swap");
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Overpass";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html {
  scroll-behavior: smooth;
  overflow-x: hidden; }

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #2c5d66;
  border: 1px solid #f1f1f1;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

@keyframes fadeout {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

@keyframes fadein {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 1; } }

.skills {
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: url(../../images/marbles.webp) no-repeat;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
  -moz-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
  box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54); }
  .skills .skills__container {
    max-width: 600px;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.658);
    -webkit-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
    -moz-box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54);
    box-shadow: 0px 0px 5px 0px rgba(143, 143, 143, 0.54); }
    .skills .skills__container .skills__ul {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      margin: 0px;
      padding: 0; }
      .skills .skills__container .skills__ul .skills__li {
        list-style: none;
        margin: 20px 30px; }
        .skills .skills__container .skills__ul .skills__li .skills__li__pic {
          animation: fadein 1.5s; }
          .skills .skills__container .skills__ul .skills__li .skills__li__pic:hover {
            animation: fadeout 1.5s; }

@media screen and (max-width: 960px) {
  .skills__container {
    border-radius: 0 !important; } }
